import React, {Component} from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";


class Home extends Component{

    render(){

        const style = {
            'text-align': 'center',
            width: '84vw'
        }
      return(

            <div style={style} className="Column">
                <div>
                    <h1>FlameHound</h1>

                    <div id ="text">
                        <p> Todos ns sabemos como os incndios assolaram Portugal no ano de 2017, com um registo mortal enorme, nunca antes visto, neste tipo de ocorrncias.</p>
                        <p> O nosso objectivo  erradicar estes acontecimentos, nunca os esquecendo mas sim utilizando-os como motor para o desenvolvimento deste projecto.</p>
                        <p> Uma actuao rpida, sem burocracia, e de cmodo uso.</p>
                        <p> Uma aplicao/pgina web, que promova o sentido comunitrio, a entre ajuda na preveno e na luta contra o desflorestamento do nosso pas.</p>
                        <p> Deste modo promovemos uma nova ferramenta que entidades podem utilizar ou mesmo, no futuro, promover.</p>
                    </div>

                    <br/>
                    <h2 id = "title1">Com a sua ajuda, conseguimos farejar os fogos!</h2>
                    <br/>
                </div>
            </div>



      );
    }
}

export default Home;


